@font-face {
  font-family: "jmh";
  src: url("../src/fonts/jmh_psychedelic_caps-webfont.woff") format("woff"), url("../src/fonts/jmh_psychedelic_caps-webfont.woff2") format("woff2");
}

:root {
  --mainbg: url("../src/images/bg2.png");
  --gradient1: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.75) 0%,
    rgba(255, 75, 43, 0.75) 100%
  );
  --gradient2: linear-gradient(
    to bottom right,
    rgba(195, 20, 50, 0.75) 0%,
    rgba(36, 11, 54, 0.75) 100%
  );
  --gradient3: linear-gradient(
    to bottom right,
    rgba(255, 65, 108, 0.25) 0%,
    rgba(255, 75, 43, 0.25) 100%
  );
  --font1: "jmh";
  --font2: "Nova Mono", monospace;
  --font3: "Quicksand", sans-serif;
  --unit: 8px;
  --mclr: 195, 20, 50;
  --hclr: 207, 139, 243;
  --gclr: 253, 185, 155;
  --c1: 167, 112, 239;
  --c2: 207, 139, 243;
  --c3: 253, 185, 155;
  --hx1: #ff416c;
  --hx2: #c31432;
  --hx3: #240b36;
  --x4: #40e0d0;
  --x5: #ff8c00;
  --x6: #ff0080;
}

img[src*=twemoji],
img[src*=animoji] {
  height: 1em !important;
  width: 1em !important;
  margin: 0 0.05em 0 0.1em;
  vertical-align: -0.1em !important;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.insta-profile {
  width: 450px;
  background: #111;
  border: 1px solid #ddd;
  margin: 0 auto;
  margin-bottom: 4rem;
  border-radius: 3px;
  overflow: hidden;
}

.insta-profile .closers {
  padding: 15px;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #333;
}

.insta-profile .closers .flw {
  font-family: var(--font4);
  font-size: 12px;
  text-transform: lowercase;
  display: inline-block;
  border-radius: 2px;
  padding: 4px 6px;
  cursor: crosshair;
  background: #fff linear-gradient(to bottom right, rgba(var(--mclr), 0.5) 0%, rgba(var(--hclr), 0.5) 50%, rgba(var(--gclr), 0.5) 100%);
}

.insta-profile .closers red {
  background-color: #ff605c;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  display: inline-block;
}

.insta-profile .closers yellow {
  background-color: #ffbd44;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  display: inline-block;
}

.insta-profile .closers green {
  background-color: #00ca4e;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  display: inline-block;
}

.insta-profile .bottomrow {
  width: 100%;
  background: #fff linear-gradient(-45deg, rgba(var(--mclr), 0.5) 0%, rgba(var(--hclr), 0.5) 50%, rgba(var(--gclr), 0.5) 100%);
  border-top: 1px solid #ddd;
  padding: 15px;
  text-align: center;
  font-family: var(--font5);
  font-weight: 500;
  text-transform: uppercase;
  font-size: 10px;
  letter-spacing: 1px;
}

.insta-profile .profile-bios {
  background: #fff;
  padding: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.insta-profile .profile-bios .imgbg {
  background: linear-gradient(-45deg, rgba(var(--mclr), 1) 0%, rgba(var(--hclr), 1) 50%, rgba(var(--gclr), 1) 100%);
  width: 200px;
  height: 200px;
  border-radius: 100%;
  background-size: 400% 400%;
  animation: gradient 4s ease infinite;
  padding: 10px;
  margin-bottom: 3rem;
}

.insta-profile .profile-bios .imgbg img {
  border-radius: 100%;
  border: 5px solid #fff;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.insta-profile .profile-bios span.insta-handle {
  font-family: var(--font3);
  text-transform: uppercase;
  font-size: 22px;
}

.insta-profile .profile-bios .insta-bio {
  font-family: var(--font4);
  text-transform: lowercase;
  margin-top: 2rem;
  font-size: 12px;
}

.thrive-comm {
  width: 380px;
  border-radius: 12px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5);
  margin: 2rem auto;
  background: var(--x1) linear-gradient(120deg, rgba(var(--mclr), 0.5) 0%, rgba(var(--hclr), 0.5) 50%, rgba(var(--gclr), 0.5) 100%);
  display: flex;
  flex-direction: column;
}

.thrive-comm .top-msg {
  display: flex;
  padding: 2rem;
  justify-content: space-between;
  align-items: center;
  font-family: var(--font4);
  text-transform: lowercase;
  letter-spacing: 1px;
  font-size: 1.5rem;
}

.thrive-comm .top-msg .closers {
  display: flex;
  flex-flow: row wrap;
}

.thrive-comm .top-msg .closers red,
.thrive-comm .top-msg .closers yellow,
.thrive-comm .top-msg .closers green {
  margin-left: 0.75rem;
}

.thrive-comm .top-msg .closers red {
  background-color: #ff605c;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  display: inline-block;
}

.thrive-comm .top-msg .closers yellow {
  background-color: #ffbd44;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  display: inline-block;
}

.thrive-comm .top-msg .closers green {
  background-color: #00ca4e;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  display: inline-block;
}

.thrive-comm .to-whom {
  padding: 6rem;
}

.thrive-comm .to-whom .emoji {
  width: 65px;
  height: 65px;
  background-size: cover;
  background-image: var(--ani);
}

.thrive-comm .messages {
  padding: 4rem;
  padding-bottom: 8rem;
  padding-top: 4rem;
}

.thrive-comm .messages br {
  display: none;
}

.thrive-comm .messages p {
  background: #fff;
  color: #111;
  font-family: var(--font2);
  font-weight: 300;
  line-height: 150%;
  border-radius: 6px;
  padding: 2.5rem;
  font-size: 2rem;
  margin-bottom: 2rem;
}

.thrive-comm .messages p img {
  width: 100%;
}

.thrive-match {
  width: 450px;
  border-radius: 3px;
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.5);
  margin: 2rem auto;
  background: #111;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.thrive-match .top-msg {
  display: flex;
  padding: 2rem;
  justify-content: space-between;
  align-items: center;
  font-family: var(--font3);
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 1.25rem;
}

.thrive-match .top-msg .group-moji {
  width: 35px;
  height: 35px;
  background-size: cover;
  background-image: var(--ani);
}

.thrive-match .top-msg .closers red {
  background-color: #ff605c;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  display: inline-block;
}

.thrive-match .top-msg .closers yellow {
  background-color: #ffbd44;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  display: inline-block;
}

.thrive-match .top-msg .closers green {
  background-color: #00ca4e;
  width: 10px;
  height: 10px;
  border-radius: 100%;
  display: inline-block;
}

.thrive-match .image {
  width: 100%;
  background: #fff linear-gradient(120deg, rgba(var(--mclr), 0.5) 0%, rgba(var(--hclr), 0.5) 50%, rgba(var(--gclr), 0.5) 100%);
  padding: 2rem;
}

.thrive-match .image img {
  width: 100%;
  border-radius: 3px;
}

.thrive-match .profile name {
  padding: 4rem;
  padding-bottom: 2rem;
  display: block;
  font-family: var(--font3);
  font-size: 3rem;
  letter-spacing: 1px;
  text-transform: uppercase;
  color: #fff;
}

.thrive-match .profile name:after {
  content: "";
  display: block;
  margin-top: 2rem;
  margin-left: -4rem;
  width: 70%;
  height: 6px;
  border-radius: 6px;
  border-top-left-radius: 0;
  border-bottom-left-radius: 0;
  background: #fff linear-gradient(120deg, rgba(var(--mclr), 0.5) 0%, rgba(var(--hclr), 0.5) 50%, rgba(var(--gclr), 0.5) 100%);
}

.thrive-match .profile desc {
  display: block;
  margin: 0 auto;
  background: #111;
  margin-top: -4rem;
  margin-bottom: 2rem;
  color: #fff;
  padding: 4rem;
  font-family: var(--font1);
  font-size: 2.25rem;
  line-height: 160%;
  font-weight: 300;
}

.thrive-match .profile pdisplay {
  padding: 4rem;
  padding-top: 0;
  display: flex;
  flex-flow: row wrap;
}

.thrive-match .profile pill {
  background: #fff;
  color: #111;
  border-radius: 20px;
  padding: 1rem 2rem;
  padding-bottom: 0.8rem;
  font-family: var(--font4);
  font-size: 1.25rem;
  letter-spacing: 1px;
  text-transform: lowercase;
  margin-right: 1rem;
}

.thrive-swipe {
  width: 280px;
  border-radius: 3px;
  background: #111;
  margin: 2rem auto;
  padding: 2rem;
}

.thrive-swipe.super .swipe-icon icon {
  background-image: url("../src/https:/files.jcink.net/uploads2/thrivinrp/animoji/sparkling_heart.gif");
}

.thrive-swipe.super status:after {
  content: "Super liked";
}

.thrive-swipe.liked .swipe-icon icon {
  background-image: url("../src/https:/files.jcink.net/uploads2/thrivinrp/animoji/fire.gif");
}

.thrive-swipe.liked status:after {
  content: "Liked";
}

.thrive-swipe.disliked .swipe-icon icon {
  background-image: url("../src/https:/files.jcink.net/uploads2/thrivinrp/animoji/thumbs_down.gif");
}

.thrive-swipe.disliked status:after {
  content: "Disliked";
}

.thrive-swipe .swipe-icon {
  display: block;
  width: 60px;
  height: 60px;
  margin: 2rem auto;
}

.thrive-swipe .swipe-icon icon {
  width: 100%;
  height: 100%;
  background-size: cover;
  display: block;
}

.thrive-swipe status {
  display: block;
  text-align: center;
  padding-bottom: 2rem;
  font-family: var(--font4);
  font-size: 2rem;
  text-transform: lowercase;
  letter-spacing: 1px;
  color: #fff;
}

